export default {
	data() {
		return {
			imgList:[],//申诉图片-展示
			content:'',//申诉内容
			qiniuToken:'',
		};
	},
	methods:{
		// 上传图片
		uploadIamge(){
			let token = uni.getStorageSync('token')
			uni.chooseImage({
				count:this.loadNum,
				sizeType: ['original', 'compressed'],
				success: (res) => {
					this.loadNum = this.loadNum - res.tempFilePaths.length
					if(res.tempFilePaths.length + this.imgList.length <=9){
						for (var i = 0; i < res.tempFilePaths.length; i++) {
							let key = MD5(res.tempFilePaths[i])
							qiniuUploader.upload(res.tempFilePaths[i], (e) => {
								this.imgList.push(e.imageURL)
							}, (error) => {
								uni.showToast({
									title: '上传失败',
									icon: 'none'
								})
								var a = JSON.stringify(error);
								console.log('error: ' + a);
							}, {
								region: 'ECN',
								domain: 'https://img.bajiaostar.com',
								key: key,
								uptoken: this.qiniuToken
							})
						}
					}else{
						uni.showToast({
							icon:'none',
							title:'超出上传限制'
						})
					}
				}
			})
		},
		//预览图片
		previewImg(e){
			uni.previewImage({
				urls:this.imgList,
				current:e.currentTarget.dataset.url
			})
		},
		// 删除图片
		DelImage(e) {
			uni.showModal({
				title: '是否删除图片',
				content: '确定要删除图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	}
}
